#modal-wrapper {
    width: 100%;
    height: 100%;
    margin: auto;
    background-color: rgba(0, 0, 0, 0);
    visibility: hidden;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1;
    -webkit-transition: .3s;
    -o-transition: .3s;
    transition: .3s;
    -webkit-transform-origin: center center;
        -ms-transform-origin: center center;
            transform-origin: center center;
}

#movie-modal {
    width: 50%;
    height: 70%;
    margin: auto;
    background-color: green;
    position: fixed;
    top: 15%;
    left: 25%;
    z-index: 1;
    -webkit-transition: .5s;
    -o-transition: .5s;
    transition: .5s;
    -webkit-transform: scale(0);
        -ms-transform: scale(0);
            transform: scale(0);
}


